body {
  background-color: #fafafa;
  margin: 0;
  font-family: Segoe UI, sans-serif;
}

.login-card {
  text-align: center;
  background-color: #fff;
  border-radius: 12px;
  width: 320px;
  margin: 5rem auto;
  padding: 2rem;
  box-shadow: 0 4px 12px #0000001a;
}

.login-card h2 {
  margin-bottom: 1.5rem;
}

.login-card input {
  border: 1px solid #ccc;
  border-radius: 8px;
  width: 100%;
  margin-bottom: 1rem;
  padding: .6rem;
  font-size: 1rem;
  display: block;
}

.login-card button {
  width: 100%;
}

.login-card a {
  color: #2f2fff;
  margin-top: 1rem;
  font-size: .9rem;
  text-decoration: none;
  display: block;
}

.register-card {
  text-align: center;
  background-color: #fff;
  border-radius: 12px;
  width: 360px;
  margin: 4rem auto;
  padding: 2rem;
  box-shadow: 0 4px 12px #0000001a;
}

.register-card h2 {
  margin-bottom: 1.5rem;
}

.register-card input {
  border: 1px solid #ccc;
  border-radius: 8px;
  width: 100%;
  margin-bottom: 1rem;
  padding: .6rem;
  font-size: 1rem;
  display: block;
}

.avatar-options {
  justify-content: space-around;
  margin-bottom: 1rem;
  display: flex;
}

.avatar-options label {
  cursor: pointer;
}

.avatar-options img {
  border: 2px solid #0000;
  border-radius: 50%;
  width: 60px;
  transition: border .2s;
}

.avatar-options input:checked + img {
  border-color: #e1306c;
}

.register-card button {
  width: 100%;
  margin-top: 1rem;
}

.app-layout {
  flex-direction: row;
  gap: 1rem;
  padding: 1rem;
  display: flex;
}

.profile-card {
  background: #fff;
  border-radius: 12px;
  flex-direction: column;
  align-items: center;
  width: 220px;
  padding: 1rem;
  display: flex;
  box-shadow: 0 2px 8px #0000001a;
}

.profile-card img {
  border-radius: 50%;
  width: 100px;
  margin-bottom: 10px;
}

button {
  color: #fff;
  cursor: pointer;
  background: linear-gradient(135deg, #feda75, #fa7e1e, #d62976, #962fbf);
  border: none;
  border-radius: 20px;
  margin-top: .5rem;
  padding: .5rem 1rem;
  font-weight: bold;
}

button:hover {
  opacity: .9;
}

.status-form {
  background: #fff;
  border-radius: 10px;
  width: 100%;
  max-width: 600px;
  margin: auto;
  padding: 1rem;
  box-shadow: 0 1px 4px #0000001a;
}

.status-form textarea {
  resize: none;
  border: 1px solid #ccc;
  border-radius: 10px;
  width: 100%;
  height: 80px;
  margin-bottom: 10px;
  padding: .5rem;
}

.status-post {
  background-color: #f1f1f1;
  border-radius: 20px;
  width: 100%;
  max-width: 600px;
  margin-top: 1rem;
  margin-left: auto;
  margin-right: auto;
  padding: 1rem;
  box-shadow: 0 2px 5px #0000000d;
}

.user-list {
  background-color: #fff;
  border-radius: 10px;
  width: 220px;
  height: fit-content;
  padding: 1rem;
  box-shadow: 0 2px 8px #0000001a;
}

.user-list-item {
  cursor: pointer;
  align-items: center;
  gap: 10px;
  margin: .5rem 0;
  display: flex;
}

.user-list-item img {
  border-radius: 50%;
  width: 35px;
  height: 35px;
}

.logged-in-tag {
  color: #555;
  margin-bottom: 1rem;
  font-size: .9rem;
}

@media screen and (width <= 768px) {
  .app-layout {
    flex-direction: column;
    align-items: center;
  }

  .profile-card, .user-list, .status-form, .status-post {
    width: 90%;
  }
}
